body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-dm-sans: "DM Sans";
  --font-dm-serif-display: "DM Serif Display";

  /* font sizes */
  --font-size-smi: 13px;
  --font-size-6xs: 7px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --color-coral-100: #fd853a;
  --color-coral-200: rgba(253, 133, 58, 0.62);
  --color-tomato: rgba(253, 71, 71, 0.4);
  --color-indianred: #e14b5a;
  --color-dodgerblue: rgba(77, 138, 240, 0.28);
  --color-steelblue: #005386;
  --color-gold: rgba(255, 208, 55, 0.59);
  --color-orange: #ffa927;
  --color-black: #000;
  --color-dimgray: #727272;

  /* Paddings */
  --padding-xs: 12px;

  /* border radiuses */
  --br-mini: 15px;
}
